const keys = ['q', 'w', 'e', 'r', 't', 'y', 'a', 's', 'd', 'f'];
        let currentKeyIndex = 0;

        const keyElement = document.querySelector(".key");
        const newGameButton = document.querySelector(".newGame");

        function setNewKey() {
            currentKeyIndex = Math.floor(Math.random() * keys.length);
            keyElement.textContent = keys[currentKeyIndex];
            PNotify.success({ text: "Гра розпочата! Натисніть правильну клавішу." });
        }

        newGameButton.addEventListener("click", setNewKey);

        window.addEventListener("keydown", (event) => {
            if (event.key === keys[currentKeyIndex]) {
                PNotify.success({ text: "Правильно! Натисніть наступну клавішу." });
                setNewKey();
            } else {
                PNotify.error({ text: "Помилка! Натисніть правильну клавішу." });
            }
        });

        window.addEventListener("keypress", (event) => {
            event.preventDefault();
        });

        setNewKey();

        